import React from "react";
import "./DeleteBox.scss";

function DeleteBox({ cancel, confirm }) {
  return (
    <div className="delete-box">
      <div className="delete-title">Your are about to delete all items</div>
      <div className="delete-buttons">
        <button onClick={cancel} className="button cancel">
          Cancel
        </button>
        <button onClick={confirm} className="button confirm">
          Confirm
        </button>
      </div>
    </div>
  );
}

export default DeleteBox;
