import React, { useState, useEffect } from "react";
import "./App.scss";
import Form from "./form/Form";
import List from "./list/List";
import DeleteBox from "./deletebox/DeleteBox";

function App() {
  const savedList = localStorage.getItem("list");

  const [list, setList] = useState(savedList ? JSON.parse(savedList) : []);

  useEffect(() => {
    localStorage.setItem("list", JSON.stringify(list));
  }, [list]);

  const [value, setValue] = useState("");
  const [deleteOpen, setDeleteOpen] = useState(false);
  const [focusing, setFocusing] = useState(true);

  function handleChange(e) {
    setValue(e.target.value);
  }

  function addTo() {
    let id = Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);

    value !== "" &&
      setList([
        {
          id,
          value,
        },
        ...list,
      ]);
    setValue("");
  }

  function cancel() {
    setDeleteOpen(false);
  }

  function clear() {
    setList([]);
    setDeleteOpen(false);
  }

  console.log(list);

  return (
    <div className="App">
      {deleteOpen && <DeleteBox cancel={cancel} confirm={clear} />}
      <List list={list} setList={setList} />
      <Form
        addTo={addTo}
        clear={() => setDeleteOpen(true)}
        value={value}
        list={list}
        focusing={focusing}
        onBlur={() => setFocusing(true)}
        onFocus={() => setFocusing(false)}
        handleChange={handleChange}
      />
    </div>
  );
}

export default App;
