import React from "react";
import "./Form.scss";
import { HiOutlineArrowNarrowRight } from "react-icons/hi";

function Form({
  value,
  handleChange,
  addTo,
  clear,
  list,
  focusing,
  onBlur,
  onFocus,
}) {
  return (
    <div className="form-container">
      <form onSubmit={(e) => e.preventDefault()}>
        <div className="input-container">
          <input
            type="text"
            placeholder="Add todo"
            value={value}
            onChange={handleChange}
            onBlur={onBlur}
            onFocus={onFocus}
          />
          <button onClick={addTo} type="submit" className="arrow-button">
            <HiOutlineArrowNarrowRight className="icon" />
          </button>
        </div>
        {list.length !== 0 && focusing && (
          <button onClick={clear} type="submit" className="clear-button">
            Clear
          </button>
        )}
      </form>
    </div>
  );
}

export default Form;
